@import "variables";

body{
  .asideButtons{
    display: none;
    position: fixed;
    right: 0;
    bottom: 40%;
    ul{
      li{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        margin: 10px;
        background-color: #e4f4f5;
        border-radius: 5px;
        a{
          width: 60px;
          height: 60px;
          display: flex;
          justify-content: center;
          align-items: center;
          text-decoration: none;
          i{
            color: @mainColor;
          }
        }
        .rollUp{
          font-size: 1.5em;
        }
        .phone{
          i{
            padding: 10px;
            font-size: 1em;
            border: 2px solid @mainColor;
            border-radius: 50px;
          }
        }
      }
    }
  }
}